body, html {
    background: #ECEDEF;
    margin: 0;
    padding: 0;
}
 
.container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.2);
    border-radius: 14px;
    padding-bottom: 20px;
    width: 320px;
}

.display {
    width: 100%;
    height: 60px;
    padding: 40px 0;
    background: #FF0509;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
}

.keys {
    padding: 20px 20px 0 20px;
}

.row {
    width: 280px;
    float: left;
}

.key {
    width: 60px;
    height: 60px;
    float: left;
    padding: 0;
    margin: 5px;
    box-sizing: border-box;
    background: #ecedef;
    border: none;
    font-size: 30px;
    line-height: 30px;
    border-radius: 50%;
    font-weight: 700;
    color: #5E5858;
    cursor: pointer;
}

.key:active {
    background-color: #d4d3d3;
    cursor: pointer
}

input[type=text] {
    width: 270px;
    height: 60px;
    float: left;
    padding: 0;
    box-sizing: border-box;
    border: none;
    background: none;
    color: #ffffff;
    text-align: right;
    font-weight: 700;
    font-size: 40px;
    line-height: 60px;
    margin: 0 25px;
    
   }

.equals-key {
    background: #FF0509 !important;
    color: #ffffff !important;
}

.operator {
    background: #e66a6c !important;
    color: #ffffff !important;
   }

.helper {
    background: #e4c391 !important;
    color: black !important;
   }

.helper:active {
    background: #e4d8c6 !important;
    color: black !important;
   }

.equals-key:active {
    background: #f56d70 !important;
    color: #ffffff !important;
   }

.operator:active {
    background: #f8adae !important;
    color: #ffffff !important;
   }
